body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #f8f9fa;
    height: 100vh; /* Asegura que el body ocupe toda la altura de la ventana */
}

html {
    height: 100%; /* Asegura que el html ocupe toda la altura de la ventana */
}

.dashboard {
    width: 100%;
    height: 100vh; /* Hace que el dashboard ocupe toda la altura de la pantalla */
    padding: 20px;
    background: white;
    border-radius: 0;
    box-shadow: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* Ajusta el contenido para que ocupe todo el espacio disponible */
    box-sizing: border-box;
}

.dashboard h2 {
    color: #333;
    text-align: center;
    margin-bottom: 20px;
}

.dashboard nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background: #007bff;
    color: white;
    border-radius: 0;
    margin-bottom: 20px;
}

.dashboard nav a {
    color: white;
    text-decoration: none;
    font-weight: bold;
}

.dashboard nav a:hover {
    text-decoration: underline;
}

.dashboard form {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.dashboard label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
}

.dashboard input,
.dashboard button {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border-radius: 5px;
    border: 1px solid #ddd;
    box-sizing: border-box;
}

.dashboard input:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0 0 4px rgba(0, 123, 255, 0.25);
}

.dashboard button {
    background: #007bff;
    color: white;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s;
}

.dashboard button:disabled {
    background-color: #c0c0c0;
    cursor: not-allowed;
}

.dashboard button:hover:not(:disabled) {
    background-color: #0056b3;
}

.dashboard .search {
    margin-top: 20px;
    display: flex;
    gap: 10px;
}

.dashboard .search input {
    flex: 1;
}

.dashboard p {
    margin-top: 15px;
    font-size: 14px;
    color: red;
}
